import React, { Component } from 'react';
import '../../support/css-tambahan/footer.css'
class Footer extends Component{
    render(){
        return(
          <footer className="page-footer font-small primary-color font-templates">
          {/* Copyright */}
          <div className="row">
          <div className="footer-copyright text-left py-3 col-md-4">
            <img src="../favicon.ico" width="50px"/>
          </div>
          <div className="footer-copyright text-center py-3 col-md-4 text-white"> 2019 Copyright:
            <a href="http://warehousenesia.id">Warehousenesia.id</a>
          </div>
          <div className="footer-copyright text-right py-3 col-md-4 text-white">
            <div className="pr-5">
           <a href={`ig`} className='text-white pr-3'> <i class='fab fa-instagram'></i></a>
           <a href={`twt`} className='text-white pr-3'><i class='fab fa-twitter'></i></a>
           <a href={`fb`} className='text-white pr-3'><i class='fab fa-facebook-f'></i></a>
            </div>
          </div>
            </div>
          {/* Copyright */}
        </footer>
        );
    }
}
export default Footer;